import React from "react";
import clsx from "clsx";

export type ButtonProps = React.ButtonHTMLAttributes<HTMLButtonElement> & {
  variant?: "hero" | "premium" | "secondary" | "outline";
  size?: "sm" | "md" | "lg";
  className?: string;
};

const variantClasses = {
  hero: "bg-gradient-primary text-primary-foreground font-bold shadow-glow hover:opacity-90",
  premium: "bg-gradient-secondary text-secondary-foreground font-bold shadow-glow hover:opacity-90",
  secondary: "bg-secondary text-secondary-foreground font-semibold hover:bg-secondary/80",
  outline: "border border-primary text-primary bg-transparent hover:bg-primary/10",
};

const sizeClasses = {
  sm: "px-4 py-2 text-sm rounded-lg",
  md: "px-6 py-3 text-base rounded-xl",
  lg: "px-8 py-4 text-lg rounded-2xl",
};

export const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  ({ variant = "hero", size = "md", className, ...props }, ref) => (
    <button
      ref={ref}
      className={clsx(
        "transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-primary/50",
        variantClasses[variant],
        sizeClasses[size],
        className
      )}
      {...props}
    />
  )
);
Button.displayName = "Button";

export default Button; 